.Button {
  border: none;
  background: none;
  outline: none;
  font-size: 1rem;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  padding: 0.5rem 1rem;
  background: white;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  font-size: 0.875rem; }
  .Button:hover {
    background: #f8f9fa; }
  .Button.outline {
    background: none;
    border: 1px solid white;
    color: white; }
    .Button.outline:hover {
      background: white;
      color: #38d9a9; }
  .Button.active {
    border: none;
    background: #38d9a9;
    color: white; }
    .Button.active:hover {
      background: #63e6be; }

.Button + .Button {
  margin-left: 1rem; }



