.TweetItem {
  position: relative;
  padding: 1rem;
  border: 2px solid #343a40; }
  .TweetItem .tweet-head {
    position: relative; }
    .TweetItem .tweet-head .writer {
      font-size: 0.875rem;
      color: #212529; }
      .TweetItem .tweet-head .writer .anonymous-name .hash {
        margin-left: 0.25rem;
        font-size: 0.75rem;
        color: #adb5bd; }
      .TweetItem .tweet-head .writer .username {
        font-weight: 600;
        color: inherit;
        text-decoration: none; }
    .TweetItem .tweet-head .date {
      margin-top: 0.2rem;
      color: #495057;
      font-size: 0.75rem; }
    .TweetItem .tweet-head .remove {
      cursor: pointer;
      position: absolute;
      right: 0;
      top: 0;
      color: #ced4da;
      font-size: 0.75rem; }
      .TweetItem .tweet-head .remove:hover {
        color: #f06595; }
  .TweetItem .text {
    margin-top: 1rem;
    white-space: pre; }
  .TweetItem .tags {
    margin-top: 1rem;
    color: #343a40;
    font-size: 0.75rem; }
    .TweetItem .tags .tag-item {
      margin-right: 0.25rem;
      padding: 0.25rem;
      display: -ms-inline-flexbox;
      display: inline-flex;
      background: #20c997;
      color: white;
      font-weight: 600;
      font-size: 0.75rem;
      border-radius: 4px;
      text-decoration: none; }

.TweetItem + .TweetItem {
  margin-top: 1rem; }



